import './numberinput.css';

import {useState, useEffect} from 'react';
import StackHoriz from './StackHoriz.js';
import Box from '@mui/material/Box';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';

function NumberInput(props) {
    const [quantity, setQuantity] = useState(props.value || 0);
    return (
        <Box className="item-summary-quantity-input-box">
          <StackHoriz>
            <Box className="item-summary-quantity-input-arrow-btn"
                 onClick={() => setQuantity(quantity > 0 ? quantity - 1 : 0)}><ArrowDropDownIcon sx={{
                     "margin-top": "0.688rem"
                 }}/></Box>
            <Box className="item-summary-quantity-input-line"/>
            <Box className="item-summary-quantity-input-text-box" sx={{
                "margin-top": "0.563rem"
            }}>
              <Box className="item-summary-quantity-input-text" sx={{
                  "margin-left": "3rem"
              }}>
                {quantity}
              </Box>
            </Box>
            <Box className="item-summary-quantity-input-line" sx={{
                "margin-left": "3.188rem"
            }}/>
            <Box className="item-summary-quantity-input-arrow-btn"
                 onClick={() => setQuantity(quantity + 1)}><ArrowDropUpIcon  sx={{
                     "margin-top": "0.688rem"
                 }}/>
            </Box>
          </StackHoriz>
        </Box>);
}

export default NumberInput;
