function RangeSlider(){
    const [value, setValue] = useState([50, 1000]);

    const handleChange = (_event, newValue) => {
        console.log(newValue);
        setValue(newValue);
    };

    return (
        <Slider
          value={value}
          min={50}
          max={1000}
          onChange={handleChange}
          valueLabelDisplay="on"
        />);
}

